.contact {
  padding-top: 15vh;
  margin: 2%;
  min-height: 80vh;
}
.container_contact {
  height: 80%;
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.contact_head {
  display: flex;
  flex-direction: column;
}
.contact h1 {
  font-size: 2.8em;
  font-family: serif;
  max-width: 80vw;
  background-color: #000000f6;
  color: white;
  padding: 1%;
  border-radius: 0px 15px 15px 0px;
}
.contact_head img {
  width: 55%;
  display: block;
  margin: auto;
}
.contact_body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  justify-items: center;
  min-height: 50vh;
  max-height: 100vh;
  align-items: center;
}
.contact_card {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  border: solid 1px #8c8cff64;
  margin-bottom: 1vh;
}
.contact_card a {
  color: rgba(0, 234, 255, 0.808);
  font-size: 0.8em;
  text-decoration: none;
  font-family: Helvetica;
}
.contact_card .img_card {
  width: 18%;
  margin: 1%;
}
.contact_card:hover{
  background-color:#32325a1e ;
  color: aqua;
  font-weight: bold;
  padding: .3%;
}
@media (max-height: 420px) {
  .contact {
    max-height: 150vh;
    padding-bottom: 3vh;
  }
}
@media (max-height: 540px) {
  .contact {
    max-height: 180vh;
    padding-bottom: 3vh;
  }
}
@media (min-width: 768px) {
  .contact h1 {
    max-width: 45vw;
  }
}
