.nav{
    text-decoration: none;
    text-align: center;
    display: grid;
    grid-template-columns: 7fr 3fr;
    background-color: #ffffff;
    position : fixed;
    width: 100%;
    box-shadow :0px 2px 8px 0px rgba(0,0,0,0.67);
}
.nav_item{
    font-size:1.5em;
    display: flex;
    align-items: center;
    justify-content: center;
    /* margin:10; */
}
.nav_item_img{
    margin :1vh; 
    display: flex;
    align-items: center;
    justify-content: right;
}
.img_logo{
    margin-left: 10vw;
    height: 50px;
    width: 50px;
}
.sidenav {
    height: 100%;
    width: 0;
    position: fixed;
    z-index: 1;
    top: 0;
    right:0;
    background-color: #111;
    overflow-x: hidden;
    transition: 0.5s;
}  
.sidenav .list_item {
    padding: 8px 8px 8px 32px;
    font-size: 25px;
    color: #818181;
    display: block;
    transition: 0.3s;
    text-align: center;
    margin-top: 20px;
    text-decoration: none;
}
.container-btnClose{
    display: flex;
    flex-direction: column;
    width: 100%;
    align-content: center;
    align-items: flex-end;
}
.sidenav .closebtn {
    color: #818181;
    margin-right: 4vw;
    cursor:pointer; 
    font-size: 3em;
}
.sidenav a:hover {
    color: #f1f1f1;
}
.btn_nav{
    color: #000000;
    font-size:30px;
    cursor:pointer;
}
.nav_handle{
    width: 250px; 
}
@media(min-width:768px){
    .sidenav .closebtn {
        /* position: absolute; */
        top: 0;
        font-size: 4em;
      }
    .img_logo{
        height: 85px;
        width: 85px;
    }
    .nav_handle{
        width: 400px; 
    }   
}
