.home {
  /* padding: 15px; */
  padding : 1vw;
  padding-top: 15vh;
  /* padding-top: 20vh; */
  /* background-color: #5c006311; */
  max-height: 85vh;
}
.home_header {
  display: flex;
  flex-direction: column;
  flex-direction: column-reverse;
}

.title {
  margin-top: 8%;
  margin-bottom: 8%;
  font-size: 1.7em;
  /* font-family: Arial  ; */
  font-family: serif;
  font-weight: 700;
  text-align: center;
  color: black;
}
.paragraph{
  
  width: 100%;
  color: #000010c7;
  font-size: .8em;
  font-family: verdana;
  text-align: center;
}
.img {
  display: block;
  margin: auto;
  max-width: 47vw;
}
.container_stores {
  margin-top: 10%;
  max-width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
}
@media(max-height:420px){
  .home{
    max-height: 190vh;
  }
}
@media(max-height:540px) {
  .home{
    max-height: 250vh;
  }
}

@media (min-width: 768px) {
  .home_header {
    flex-direction: row;
  }
  .img {
    height: 40%;
    width: 30%;
  }
  .home {
    padding-top: 15%;
  }
  .title {
    text-align: start;
    max-width: 60%;
    font-size: 2.5em;
  }
  .container_stores {
    margin-top: 10%;
    max-width: 50%;
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}
@media (min-width: 768px) {
  .home {
    padding-top: 10vh;
  }
  .paragraph{
    width: 50%;
  }
  .title {
    /* text-align: start; */
  }
}
@media (min-width: 1024px) {
  .home {
    padding-top: 14vh;
  }
  .title {
    text-align: center;
    font-size: 3em;
  }
  .paragraph{
    font-size:.9em;
  }
}
