.container_btn {
  width: 100%;
}
.icon_store {
  max-width: 13vw;
  margin: auto;
  /* background-color: blue; */
  display: block;
}
.button {
  border-radius: 4px;
  background-color: #932f9ade;
  border: none;
  color: white;
  font-family: helvetica;
  text-align: center;
  padding: 2% 8%;
  transition: all 0.5s;
  cursor: pointer;
  /* margin: 5px; */
  display: block;
  margin: auto;
  margin-top: 20px;
  font-size: .9em;

}
.button span {
  cursor: pointer;
  display: inline-block;
  transition: 0.5s;
}
.link{
  text-decoration: none;
}
.button span:after {
  content: "\00bb";
  opacity: 0;
  top: 0;
  right: -20px;
  transition: 0.5s;
}
.button:hover span {
  padding-right: 25px;
}
.button:hover span:after {
  opacity: 1;
  right: 0;
}
.button:active {
  color: white;
  background-color: #ffe61a;
}
.button:active:after {
  transition: 0s;
}


@media (min-width: 768px) {
  .button {
    font-size: 1.1em;
  }
}
@media (min-width: 1024px) {
  .button {
    font-size: 1.5em;
  }
}
