.card {
  margin: auto;
  min-width: 60vw;
  max-width: 80vw;
  max-height:90vh;
  padding: 1%;
  text-align: center;
  margin-bottom: 10%;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}
.card_body_s{
  height: 29vh;
  padding: 1%;
  text-align: center;
  margin-bottom: 10%;
  display: flex;
  flex-direction: column;
  font-family: arial;
}
.card_img{
  width: 27vw;
}
.price {
  color: grey;
  font-size: 1em;
}
.card h1 {
  margin-top: 2%;
  font-weight: bold;
}
.card p {
  font-size: 0.8em;
  padding: 2%;
}
.conte_img{
  
}
.card button {
  margin-top: 1vh;
  border: none;
  outline: 0;
  padding: 5%;
  color: white;
  font-family: Helvetica;  
  background-color: #922F9A;
  font-weight: 500;
  text-align: center;
  cursor: pointer;
  width: 100%;
  font-size: 1.5em;
}

@media(max-height:420px){
  .card{
      min-height:100vh ;
      max-height: 200vh;
  } 
  .card_body_s{
    height: 20vh;
  }
}
@media(max-height:540px) {
  .card{
    max-height: 250vh;
  }
  .card_body_s{
    height: 40vh;
  }
}
@media(max-height:640px){
  .card{
    width: 50vw; 
  }
}
@media(min-width:280px) {
  .card{
    width: 80px;
  }
}

@media(min-height:280px)and (orientation: landscape){
  .card{
    width: 80px;
  }
  .card_body_s{
    height: 70vh;
  }
}
@media(min-height:812px) and (orientation:portrait){
  .card_body_s{
    height: 20vh;
  }
}
@media (min-width: 768px)and (orientation:landscape){
  .card_body_s{
    height: 50vh;
  }
  .card {
    min-width: 25vw;
    margin-bottom: 5%;
  } 
  
  .card_img {
    max-width: 15vw;
  }
}
@media (min-width: 768px)and (orientation:portrait){
  .card {
    min-width: 25vw;
    margin-bottom: 5%;
  }
  .card_body_s{
    height: 20vh;
  }
  .card_img {
    max-width: 15vw;
  }
}
@media (min-width: 1024px) and (orientation:landscape) {
  .card_img {
    max-width: 8vw;
  }
  .card_body_s{
    height: 28vh;
  }
  .card button:hover {
    opacity: 0.7;
  }  
}
