.about{
  padding-top: 15vh;
}
/* Style the buttons that are used to open and close the accordion panel */
.accordion {
    background-color: #e5a8e942;
    color: #03bbbbd5;
    cursor: pointer;
    padding: 2%;
    width: 100%;
    font-size: 1.5em;
    font-family: helvetica;
    text-align: left;
    font-weight: bold;
    border: none;
    outline: none;
    transition: 0.4s;
    margin-top:5vh;
    align-items: center;
    display: flex;
  }
  .accordion span{
    color: rgb(231, 123, 248);
    font-size: 1.5em;
    text-align: center;
    align-items: center;
    margin-left: 5vw;
    margin-right:2vw ;
  }
  /* Add a background color to the button if it is clicked on (add the .active class with JS), and when you move the mouse over it (hover) */
  .active, .accordion:hover {
    background-color: #ccc;
  }
  
  /* Style the accordion panel. Note: hidden by default */
  .panel {
    display: flex;
    padding: 0 18px;
    background-color: white;
    display: none;
    overflow: hidden;
  }
  .panel p{
    margin: 2%;
    font-size: 1.2em;
    font-weight: 600;
    font-family: serif;
    color: #000000bc;

  }
  .activePanel{
    display: block;
  }
  .job_card{
    display: flex;
    flex-direction: column; 
    justify-content: center;
    align-items: center;
    min-width: 20vw;
    /* width: 30vw; */
    max-width: 100vw;
    padding: 1%;
    border-radius: 10px;
    margin-top: 1%;
    border: solid rgba(0, 255, 255, 0.561) 1px;
  }
  .job_card h1{
    font-family: helvetica;
    font-size: 1.1em;
    font-weight: 550;
    padding: 1%;
  }
  .job_card ul li{
    font-family: verdana;
    font-size: .8em;
    padding: 1%;
    list-style:initial;
    color: rgb(116, 116, 116);
  }
  .job_card a button{
    /* background-color: lightgreen; */
    padding: 10%;
    margin: 3%;
    text-align: center;
    border-radius: 9px;
    font-weight: 600;
    color: rgb(66, 66, 66);
    border: rgb(223, 35, 251) solid  0.2px;
  }
  @media(min-width:768px){
    .job_card{
      max-width: 40vw;
    }
  }
  